:root {
    --c: #d67e03;
}

#startwrap {
    display: grid;
    grid-template-areas:
        'logo'
        'houses'
        'button';
}

.logo {
    grid-area: logo;
    margin: 50px auto;
    min-width: 550px;
}

.houseswrap {
    grid-area: houses;
    display: flex;
    justify-content: space-between;
}

.houses {
    position: relative;
    width: 20%;
    align-content: center;
}

.houses:after {
    content: '';
    display: block;
    padding-bottom: 100%;
}

.housestext {
    position: absolute;
    width: 100%;
    height: 100%;
    font-family: 'Oswald', sans-serif;
    font-size: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.btn {
    background-color: transparent;
    border: white solid 5px;
    font-family: 'Oswald', sans-serif;
    font-size: 40px;
    color: white;
    cursor: pointer;
    outline: none;
    border-radius: 10px;
    text-transform: uppercase;
}

.btn:hover {
    color: #191919;
    background-color: white;
    border: white solid 5px;
}

.G:hover,
.G:focus {
    color: #d67e03;
    background-color: #6c1a31;
    border: #6c1a31 solid 5px;
}

.H:hover,
.H:focus {
    color: #5e3225;
    background-color: #ba7625;
    border: #ba7625 solid 5px;
}

.R:hover,
.R:focus {
    color: #7b9bc1;
    background-color: #063f53;
    border: #063f53 solid 5px;
}

.S:hover,
.S:focus {
    color: #8b8a8f;
    background-color: #134731;
    border: #134731 solid 5px;
}

.startbtn {
    grid-area: button;
    margin-top: 50px;
}

.logowrap {
    grid-area: logo;
    min-width: 550px;
    display: grid;
    justify-content: center;
}

.logotime {
    width: 200px;
    margin-bottom: 100px;
}

.rangewrap {
    grid-area: houses;
    display: grid;
    align-items: center;
    text-align: center;
    position: relative;
}

label[for='timeset'] {
    font-family: 'Oswald', sans-serif;
    font-size: 40px;
    color: var(--c);
    text-transform: uppercase;
}

#outputtime {
    font-family: 'Oswald', sans-serif;
    font-size: 40px;
    color: white;
    text-align: center;
    margin-bottom: 50px;
}

input[type='range'] {
    display: block;
    width: 100%;
    margin: 50px 0;
    -webkit-appearance: none;
    background: transparent;
    outline: none;
  }

  input[type='range']::-webkit-slider-runnable-track {
    height: 0.9rem;
    margin: 0;
    width: 100%;
    cursor: pointer;
    background: var(--c);
    border-radius: 50px;
    height: 5px;
  }
  
  input[type='range']::-moz-range-track {
    height: 0.9rem;
    margin: 0;
    width: 100%;
    cursor: pointer;
    background: var(--c);
    border-radius: 50px;
    height: 5px;
  }
  
  input[type='range']::-ms-track {
    height: 0.9rem;
    margin: 0;
    width: 100%;
    cursor: pointer;
    color: transparent;
    background: var(--c);
    border-radius: 50px;
    height: 5px;
  }
  
  input[type='range']::-ms-fill-lower {
    background: transparent;
  }

  input[type=range]::-webkit-slider-thumb {
    -webkit-appearance: none;
    height: 1.5rem;
    width: 1.5rem;
    border-radius: .75rem;
    background: var(--c);
    cursor: pointer;
    margin-top: -10px;
  }
  
  input[type=range]::-moz-range-thumb {
    -webkit-appearance: none;
    height: 1.5rem;
    width: 1.5rem;
    border-radius: 0.75rem;
    background: var(--c);
    cursor: pointer;
    margin-top: -14px;
    border: transparent;
  }
  
  input[type=range]::-ms-thumb {
    height: 1.5rem;
    width: 1.5rem;
    border-radius: 0.75rem;
    background: var(--c);
    cursor: pointer;
    margin-top: -14px;
  }